		recipe_content:
			'Zutaten fr eine runde Auflaufform<br/><br/>Den Ofen auf 200C vorheizen. <strong>3 Eier</strong> trennen und die Eiweie steif schlagen. Die Eigelbe mit <strong>100 g Mehl, 175 ml Milch, 30 g Zucker, 250 g Quark</strong> und <strong>1 EL Vanillezucker</strong> verquirlen und anschlieend das Eiwei unterheben.<br/><br/><strong>500 g TK Beeren</strong>(hier Himbeeren) in einem Topf mit 1 Tasse Wasser und dem <strong>Mark von 1 Vanilleschote</strong> aufkochen und dann mit etwas <strong>Mondamin</strong> sehr dick andicken.<br/><br/>Die Form mit <strong>Butter</strong> einfetten und die Beeren hineingeben. Den Teig darber gieen und fr etwa 20 Minuten backen, bis die Oberflche fest ist. Mit <strong>Puderzucker</strong> bestreuen.',
		recipe_title: 'Auarkauflauf mit Beeren',
