		recipe_content:
			'fr 4<br/><strong>Auberginen aus dem Ofen </strong><br/><br/><strong>4 Auberginen</strong> halbieren und aushhlen. Das Fruchtfleisch und die Hlften salzen und 30 Minuten ziehen lassen.<br/><strong>3 Zwiebeln</strong> und <strong>2 Knoblauchzehen</strong> in Wrfel schneiden. <strong>8 Tomaten</strong> ebenfalls in Stcke schneiden. Etwas <strong>Thymian</strong> kleinzupfen.<br/><strong>2 El Olivenl</strong> in erhitzen. Zwiebel und Knoblauch andnsten. Das Fruchfleisch der Aubergine ausdrcken, in Wrfel schneiden und ebenfalls dazugeben. <strong>1 EL Tomatenmark</strong> und <strong>1 TL Paprikapulver</strong> zum Gemse geben. Tomaten, <strong>1 Dose gehackte Tomaten</strong> und Thymian dazugeben und noch 3 Minuten mitdnsten. <strong>1 PK Feta</strong> in Wrfel schneiden und unterheben.<br/>Fllung mit Salz, Pfeffer und <strong>Zitronensaft</strong> (alternativ Essig) abschmecken.<br/>Backofen auf 200 Grad vorheizen.<br/><strong>100 ml Gemsebrhe</strong> in eine Auflaufform gieen. Die Auberginenhlften in die Form setzten und mit der Masse fllen.<br/>30 Minuten im Ofen garen.<br/><br/><strong>Hirse Schnitten </strong><br/><br/><strong>1 Zitrone</strong> auspressen, mit <strong>1 fein gehackter Knoblauchzehe</strong> und <strong>1 Bund Petersilie</strong> vermischen und ziehen lassen.<br/><strong>200 g Hirse</strong> in <strong>1 l Gemsebrhe</strong> 20 Minuten zugedeckt kcheln, dabei mehrmals umrhren.<br/><strong>2 EL Butter</strong> und <strong>50 g Parmesan</strong> untermischen. Die Krutermischung ebenfalls unterrhren. 1 cm dick auf ein mit Backpapier belegtem Blech streichen. Bei 180 Grad 15 Minuten im Backofen backen.',
