			'<strong>220 g Mehl, 1/2 TL Backpulver, 75 g Zucker, 150 g Butter, 1 Prise Salz</strong> und <strong>1 Eigelb </strong>mit den Knethaken des Handrhrers zu einem glatten Teig verarbeiten. Auf einem gefetteten Blech (30x40 cm) bis in die Ecken ausrollen und 30 Minuten kalt stellen.<br/><strong>4 El Wasser, 2 EL Honig, 100 g Zucker, 100 g Butter</strong> und <strong>1 Prise Salz</strong> in einem Topf aufkochen. <strong>200 g Haselnsse</strong> und <strong>200 g Mandeln</strong> untermischen und die Masse abkhlen lassen.<br/>Den Teig mehrmals mit einer Gabel einstechen. Die Nussmasse gleichmig auf dem Teig verteilen und leicht andrcken. Im vorgeheizten Ofen bei 200 Grad (Umluft 180 Grad) auf der 2. Schiene von unten 15-20 Minuten backen.<br/>Die Teigplatte etwas abkhlen lassen. Lauwarm schneiden. Vollstndig abkhlen lassen und erst dann am besten mit einer Palette vom Blech nehmen.',
