			'<strong>300 g Couscous</strong> mit kochendem Wasser bedecken. <strong>3 EL Tomatenmark, 1 Tl Brhe</strong> und <strong>1 TL Paprikapulver</strong> unterrhren. Mit <strong>1 gepressten Knoblauchzehe</strong> vermengen. Ziehen lassen.<br/><br/><strong>300 g Mhren</strong> lngs in Streifen hobeln (mit einem Schlmesser). <strong>4 Zucchini</strong> in Scheiben schneiden. <strong>2 Auberginen</strong> wrfeln. Das Gemse mit <strong>4 EL l</strong> mischen und auf einem Backblech fr 30 Minuten bei 200 Grad backen. In der Zwischenzeit <strong>3 Frhlingszwiebeln</strong> und <strong>1 Bund Petersilie</strong> in Streifen schneiden und unter den Couscous mischen. 1 Zitrone auspressen und ebenfalls unterrhren. <strong>100 g Feta</strong> in Wrfel schneiden und mit dem Ofengemse unter den Couscous mischen.<br/>Mit Salz und Pfeffer abschmecken.',
