			'Den Ofen auf 170C Ober- und Unterhitze vorheizen und eine Gugelform fetten und mit Mehl bestuben. <strong>200 g Mehl, 1/4 TL Backpulver, 1 TL Backpulver, 1 Prise Salz</strong> und<strong> 1/2 TL Zimt</strong> mischen und sieben.<strong>120 ml l, 100 g brauner Zucker</strong> und <strong>2 Eier</strong> mit einem Handrhrgert gut durchmixen. <strong>3 reife Bananen</strong> zu Mus zerdrcken und dazu geben. Dann die Mehlmischung und so lange rhren, bis das Mehl eingearbeitet ist. In den Ofen geben und ca. 50-60 Minuten lang backen.',
