		recipe_content:
			'Bestandteile:<br/><strong>Zucchini, Feta, Coctailtomaten, rote Zwiebeln, Auberginen, Mhren, Knoblauchzehen</strong><br/><br/>Zunchst bereitet ihr das Gemse zu. Die Auberginen quer in Scheiben schneiden und nebeneinander ausgebreitet mit Salz bestreut. 15 Minuten ziehen lassen.Den Backofen auf 180 Grad vorheizen und ein groes Backblech mit Backpapier auslegen. In der Zwischenzeit knnt ihr euch an das restliche Gemse begeben.<br/>Zucchini und Mhren lngs in 1 cm dicke Scheiben schneiden. Die Cocktailtomaten halbieren. Die roten Zwiebeln huten und ebenfalls halbieren. Knoblauchzehen (2) fein wrfeln. Das Gemse in eine Schssel geben und mit reichlich Olivenl vermengen. Mit Salz und Pfeffer wrzen. Nebeneinander auf dem Backblech verteilen.<br/>Nun die Auberginen mit einem Kchentuch abtupfen und ebenfalls auf das Blech legen. Mit Olivenl bestreichen.<br/>Das Gemse fr 40 Minuten in den Ofen geben. Es sollte schn weich garen, unter Umstnden dauert das auch etwas lnger. Am Besten im Blick behalten und das Gemse einmal wenden.<br/><br/>Fr den Hummus:<br/><strong>1 Dose Kichererbsen</strong> im Sieb abbrausen. <strong>2 Knoblauchzehen</strong> wrfeln. Dann die Kichererbsen mit Knoblauch, <strong>3 EL Olivenl, 3 EL Zitronensaft</strong> und <strong>2-3 EL Wasser</strong> zu einer Paste prieren. Mit Salz und etwas Pfeffer abschmecken.<br/><br/>Zu guter Letzt wird der Feta noch in Scheiben geschnitten und mit dem Gemse auf einem Teller angerichtet. Den Humus in eine kleine Schssel geben und mit Sonnenblumenkernen bestreuen.',
