			'Zuerst <strong>1 Aubergine</strong> halbieren. Ein Backblech mit <strong>Olivenl</strong> bestreichen und leicht salzen und pfeffern. Die Haut der Auberginen mit einer Gabel mehrmals einstechen und mit der Flche nach unten auf das Backblech legen. Im Ofen bei 200 Grad 20 - 30 Minuten weich garen.<br/><br/>In der Zwischenzeit <strong>ein groes (!!) Stck Lachs</strong> in eine gelte Auflaufform legen. Mit etwas Olivenl einreiben, salzen und mit <strong>1 - 2 TL braunem Zucker</strong> bestreuen. <strong>200 g Kirschtomaten</strong> halbieren, mit <strong>Olivenl</strong> und <strong>1 TL braunem Zucker</strong> bestreuen. <strong>1 Knoblauchzehe</strong> in feine Scheiben schneiden und mit den Tomaten zu dem Lachs in die Auflaufform geben.<br/><br/><strong>250 g Pasta</strong> in Salzwasser kochen, abtropfen lassen.<br/><br/>Wenn die Auberginen weich gegart sind, diese mit einem Lffel aushhlen, sodass ein Rand stehen bleibt. Fr die Fllung das Fruchtfleisch, <strong>300 g Kirschtomaten, 1 rote Zwiebel, 1 Knoblauchzehe</strong> und <strong>200 g Feta</strong> fein wrfeln. <strong>1/2 Bund Petersilie</strong> fein hacken und unter die Mischung geben. Vorsichtig mit Salz, Pfeffer und <strong>2 EL Zitronensaft</strong> wrzen.<br/><strong>2 EL Sonnenblumenkerne</strong> untermengen. Die Fllung auf die Auberginenhlften verteilen und diese ebenfalls in eine Auflaufform setzen.<br/><br/>Zusammen mit dem Lachs fr 20 Minuten in den Ofen geben.<br/><br/><strong>1/2 Bund Petersilie</strong> fein hacken. <strong>2 EL Butter</strong> in einer Pfanne erhitzen und die Nudeln und Petersilie hinzugeben, leicht salzen und gut vermengen.<br/><br/>Alles auf zwei Tellern anrichten',
