		recipe_content:
			'Den Backofen auf 160 Grad Umluft vorheizen. <strong>450 g Mhren</strong> fein reiben. <strong>1 Zitrone</strong> auspressen und unter die Mhren rhren. <strong>5 Eier</strong> mit <strong>150 g Zucker </strong>5 Minuten schaumig schlagen. <strong>100 g gemahlene Mandeln</strong> mit der Eier-Zucker-Masse vermengen. <strong>175 g Mehl</strong> und<strong> 1 TL Backpulver</strong> vermengen und auf den Teig sieben. Die Mhren hinzugeben und alles gut vermischen. Den Teig in eine Springform (24 cm) geben und im heien Backofen 30 Minuten backen.<br/>Fr das Topping <strong>200 g Sahne</strong> steif schlagen. <strong>150 g Quark</strong> mit <strong>1 PK Vanillezucker</strong> glattrhren und die Sahne vorsichtig unterheben. Die Masse auf dem ausgekhlten Kuchen verteilen und mit <strong>Haselnssen</strong> dekorieren.',
