			'Fr 4.<br/><strong>400 g Mehl</strong> in eine Schssel sieben. <strong>1 TL Salz, 1 Ei </strong>und<strong> 1/8 l Wasser</strong> mit dem Mehl zu einem Teig verkneten. 30 Minuten ruhen lassen.<br/><strong>1 Zwiebel </strong>schlen, fein wrfeln. <strong>1 EL Butter</strong> erhitzen, Zwiebel darin glasig dnsten. <strong>250 g vakuumierte Rote Bete</strong> auf einer Kchenreibe fein reiben. <strong>1 Ei</strong> trennen. <strong>Zwiebel, Rote Bete, 1 Handvoll Parmesan, Eigelb</strong> und <strong>50 g Frischkse </strong>verrhren. Mit Salz und Pfeffer wrzen.<br/>Nudelteig dnn ausrollen und in lange Bahnen scheiden. Auf den Streifen die Fllung in kleinen Mengen setzen. Mit einem anderen Streifen bedecken. Rund um die Fllung die Seiten zusammendrcken, sodass kleine Rechtecke entstehen.<br/>In einem weiten Topf 3-4 l Salzwasser (ca. 1 TL Salz pro Liter) aufkochen. Hlfte Ravioli darin bei leicht kchelndem Wasser ca. 5 Minuten garen. Die fertigen Ravioli mit einer Schaumkelle herausheben und gut abtropfen lassen. brige Ravioli ebenso garen und abtropfen lassen.<br/>Inzwischen fr die Nussbutter<strong> 100 g Walnsse </strong>grob hacken.<strong> 4 EL Butter</strong> in einer groen Pfanne erhitzen. Nsse darin unter Wenden anrsten. Butter mit Salz wrzen. Ravioli portionsweise in der Walnussbutter schwenken.<br/><br/>gersteter Rosenkohl<br/>Den Stiel und die ueren Bltter von <strong>1 kg Rosenkohl</strong> entfernen. Den Rosenkohl halbieren. In einer Schssel mit <strong>4 EL Olivenl </strong>und <strong>2 EL Zitronensaft</strong> vermengen. Mit Salz und Pfeffer wrzen. Im heien Ofen bei 200 Grad 20 - 30 Minuten rsten.',
